.circular-image-small {
  height: 36px;
  width: 36px;
  border-radius: 50%;
}
.avatar-image-small {
  width: 54px;
  height: 54px;
  border-radius: 12px;
}
.rating-container {
  margin-right: 10px;
  margin-left: 2rem;
  .name {
    color: #30304e;
  }
  .star {
    font-size: 18px;
    color: #ff6869;
  }
  .value {
    color: #8d8d8d;
    font-size: 14px;
    margin-top: 2px;
    margin-left: 5px;
  }
}
.search-container {
  width: 300px;
  .icon {
    color: #717171;
    margin-left: 3rem;
    margin-right: 1rem;
    font-size: 25px;
  }
}

